import React from 'react';
import { render, waitFor, fireEvent } from '@testing-library/react-native';
import { router } from 'expo-router';

// Screen to test
import VocabularyRevision from '../../src/screens/VocabularyRevision';

// Mock expo-router
jest.mock('expo-router', () => ({
  useFocusEffect: jest.fn(callback => callback()),
  router: {
    push: jest.fn(),
    back: jest.fn(),
  },
  Stack: ({ children }) => <>{children}</>,
  useLocalSearchParams: () => ({}),
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
    canGoBack: jest.fn(() => true),
    addListener: jest.fn(),
    isFocused: jest.fn(() => true),
  }),
}));

// Mock data for revision questions
const mockRevisionQuestions = [
  { 
    id: 'q1', 
    type: 'word', 
    word: 'testWord1', 
    options: ['correct1', 'wrong1', 'wrong2', 'wrong3'], 
    correctAnswer: 'correct1' 
  },
  { 
    id: 'q2', 
    type: 'word', 
    word: 'testWord2', 
    options: ['wrong1', 'correct2', 'wrong2', 'wrong3'], 
    correctAnswer: 'correct2' 
  },
  { 
    id: 'q3', 
    type: 'word', 
    word: 'testWord3', 
    options: ['wrong1', 'wrong2', 'correct3', 'wrong3'], 
    correctAnswer: 'correct3' 
  },
];

// Mock useRevisionData hook
jest.mock('../../src/hooks/useRevisionData', () => {
  return jest.fn(() => ({
    revisionQuestions: [],
    isLoading: false,
    error: null,
    stats: { totalLearned: 0 },
    hasEnoughWords: false,
    canGenerateQuestions: false,
  }));
});

// Mock useQuizEngine hook
jest.mock('../../src/hooks/useQuizEngine', () => {
  return jest.fn(() => ({
    currentQuestion: null,
    isFinished: false,
    score: 0,
    totalQuestions: 0,
    showResult: false,
    goToNextQuestion: jest.fn(),
    handleAnswer: jest.fn(),
    handleRestart: jest.fn(),
  }));
});

// Mock useRevisionManager hook
const mockMarkRevisionCompleted = jest.fn();
jest.mock('../../src/hooks/useRevisionManager', () => {
  return jest.fn(() => ({
    markRevisionCompleted: mockMarkRevisionCompleted,
  }));
});

// Import the mocked hooks
const useRevisionData = require('../../src/hooks/useRevisionData');
const useQuizEngine = require('../../src/hooks/useQuizEngine');

describe('Test d\'intégration: Parcours de révision de vocabulaire', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    // Reset mocks to default values
    useRevisionData.mockReturnValue({
      revisionQuestions: [],
      isLoading: false,
      error: null,
      stats: { totalLearned: 0 },
      hasEnoughWords: false,
      canGenerateQuestions: false,
    });
    
    useQuizEngine.mockReturnValue({
      currentQuestion: null,
      isFinished: false,
      score: 0,
      totalQuestions: 0,
      showResult: false,
      goToNextQuestion: jest.fn(),
      handleAnswer: jest.fn(),
      handleRestart: jest.fn(),
    });
  });

  describe('Chargement et affichage des questions', () => {
    test('Affiche la première question de révision correctement', async () => {
      // Mock: données de révision disponibles
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      // Mock: état initial du quiz
      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[0],
        isFinished: false,
        score: 0,
        totalQuestions: mockRevisionQuestions.length,
        showResult: false,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        // Vérifier que la première question est affichée
        expect(getByText('testWord1')).toBeTruthy();
        
        // Vérifier que les options sont affichées
        expect(getByText('correct1')).toBeTruthy();
        expect(getByText('wrong1')).toBeTruthy();
        expect(getByText('wrong2')).toBeTruthy();
        expect(getByText('wrong3')).toBeTruthy();
      });
    });

    test('Affiche un état de chargement quand les données sont en cours de récupération', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: [],
        isLoading: true,
        error: null,
        stats: { totalLearned: 0 },
        hasEnoughWords: false,
        canGenerateQuestions: false,
      });

      const { getByTestId } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByTestId('loading-indicator')).toBeTruthy();
      });
    });

    test('Affiche un message d\'erreur quand il n\'y a pas assez de mots', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: [],
        isLoading: false,
        error: null,
        stats: { totalLearned: 2 },
        hasEnoughWords: false,
        canGenerateQuestions: false,
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 10 } }} />
      );

      await waitFor(() => {
        expect(getByText(/pas assez de mots/i) || getByText(/insuffisant/i)).toBeTruthy();
      });
    });
  });

  describe('Gestion des réponses et navigation', () => {
    test('Gère correctement une réponse correcte', async () => {
      const mockHandleAnswer = jest.fn(() => true);

      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[0],
        isFinished: false,
        score: 0,
        totalQuestions: mockRevisionQuestions.length,
        showResult: false,
        goToNextQuestion: jest.fn(),
        handleAnswer: mockHandleAnswer,
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByText('testWord1')).toBeTruthy();
      });

      // Simuler une réponse correcte
      fireEvent.press(getByText('correct1'));

      // Vérifier que handleAnswer a été appelé avec la bonne réponse
      expect(mockHandleAnswer).toHaveBeenCalledWith('correct1');
    });

    test('Passe à la question suivante après avoir répondu', async () => {
      const mockGoToNextQuestion = jest.fn();

      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[0],
        isFinished: false,
        score: 0,
        totalQuestions: mockRevisionQuestions.length,
        showResult: false,
        goToNextQuestion: mockGoToNextQuestion,
        handleAnswer: jest.fn(() => true),
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByText('testWord1')).toBeTruthy();
      });

      // Répondre à la question
      fireEvent.press(getByText('correct1'));

      // Simuler le passage à la question suivante
      const continueButton = getByText('Continuer') || getByText('Suivant');
      if (continueButton) {
        fireEvent.press(continueButton);
        expect(mockGoToNextQuestion).toHaveBeenCalled();
      }
    });
  });

  describe('Affichage des résultats finaux', () => {
    test('Affiche les résultats finaux après la dernière question', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[2], // Dernière question
        isFinished: true,
        score: 2,
        totalQuestions: mockRevisionQuestions.length,
        showResult: true,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        // Vérifier l'affichage des résultats
        expect(getByText(/Résultats/i) || getByText(/Terminé/i)).toBeTruthy();
        expect(getByText(/Score/i) || getByText(/2.*3/)).toBeTruthy();
      });
    });

    test('Sauvegarde les résultats de la révision', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[2],
        isFinished: true,
        score: 3,
        totalQuestions: mockRevisionQuestions.length,
        showResult: true,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: jest.fn(),
      });

      render(<VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />);

      await waitFor(() => {
        // Vérifier que markRevisionCompleted a été appelé
        expect(mockMarkRevisionCompleted).toHaveBeenCalledWith(
          mockRevisionQuestions,
          3, // score
          3  // totalQuestions
        );
      });
    });

    test('Permet de terminer et retourner à l\'écran précédent', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[2],
        isFinished: true,
        score: 1,
        totalQuestions: mockRevisionQuestions.length,
        showResult: true,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByText(/Résultats/i) || getByText(/Terminé/i)).toBeTruthy();
      });

      // Simuler la fin de la révision
      const finishButton = getByText('Terminer') || getByText('Fermer') || getByText('Retour');
      if (finishButton) {
        fireEvent.press(finishButton);
        expect(router.back).toHaveBeenCalled();
      }
    });

    test('Affiche le pourcentage de réussite correct', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[2],
        isFinished: true,
        score: 2,
        totalQuestions: 3,
        showResult: true,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: jest.fn(),
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        // Vérifier l'affichage du score (2/3 = ~67%)
        expect(
          getByText(/67%/i) || 
          getByText(/2.*3/) ||
          getByText(/Score.*2/)
        ).toBeTruthy();
      });
    });
  });

  describe('Gestion des erreurs et cas limites', () => {
    test('Gère gracieusement les erreurs de chargement des données', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: [],
        isLoading: false,
        error: 'Erreur de chargement des données de révision',
        stats: { totalLearned: 0 },
        hasEnoughWords: false,
        canGenerateQuestions: false,
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByText(/Erreur/i) || getByText(/problème/i)).toBeTruthy();
      });
    });

    test('Gère correctement le redémarrage de la révision', async () => {
      const mockHandleRestart = jest.fn();

      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[2],
        isFinished: true,
        score: 1,
        totalQuestions: mockRevisionQuestions.length,
        showResult: true,
        goToNextQuestion: jest.fn(),
        handleAnswer: jest.fn(),
        handleRestart: mockHandleRestart,
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      await waitFor(() => {
        expect(getByText(/Résultats/i)).toBeTruthy();
      });

      // Simuler le redémarrage
      const restartButton = getByText('Recommencer') || getByText('Refaire');
      if (restartButton) {
        fireEvent.press(restartButton);
        expect(mockHandleRestart).toHaveBeenCalled();
      }
    });

    test('Gère les paramètres de route invalides', async () => {
      useRevisionData.mockReturnValue({
        revisionQuestions: [],
        isLoading: false,
        error: null,
        stats: { totalLearned: 0 },
        hasEnoughWords: false,
        canGenerateQuestions: false,
      });

      const { getByText } = render(
        <VocabularyRevision route={{ params: { level: 'invalid', questionsCount: -1 } }} />
      );

      await waitFor(() => {
        // Doit afficher un message d'erreur ou un état vide contrôlé
        expect(
          getByText(/Aucune question/i) || 
          getByText(/Paramètres invalides/i) ||
          getByText(/pas assez de mots/i)
        ).toBeTruthy();
      });
    });
  });

  describe('Parcours complet end-to-end', () => {
    test('Complète un parcours de révision complet avec succès', async () => {
      const mockGoToNextQuestion = jest.fn();
      const mockHandleAnswer = jest.fn(() => true);

      // Premier état : première question
      useRevisionData.mockReturnValue({
        revisionQuestions: mockRevisionQuestions,
        isLoading: false,
        error: null,
        stats: { totalLearned: 100 },
        hasEnoughWords: true,
        canGenerateQuestions: true,
      });

      useQuizEngine.mockReturnValue({
        currentQuestion: mockRevisionQuestions[0],
        isFinished: false,
        score: 0,
        totalQuestions: mockRevisionQuestions.length,
        showResult: false,
        goToNextQuestion: mockGoToNextQuestion,
        handleAnswer: mockHandleAnswer,
        handleRestart: jest.fn(),
      });

      const { getByText, rerender } = render(
        <VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />
      );

      // 1. Vérifier l'affichage de la première question
      await waitFor(() => {
        expect(getByText('testWord1')).toBeTruthy();
      });

      // 2. Répondre correctement à la première question
      fireEvent.press(getByText('correct1'));

      // Simuler l'état après avoir répondu (résultats finaux)
      useQuizEngine.mockReturnValue({
        currentQuestion: null,
        isFinished: true,
        score: 2,
        totalQuestions: mockRevisionQuestions.length,
        showResult: true,
        goToNextQuestion: mockGoToNextQuestion,
        handleAnswer: mockHandleAnswer,
        handleRestart: jest.fn(),
      });

      // Re-render pour simuler la mise à jour d'état
      rerender(<VocabularyRevision route={{ params: { level: 'mixed', questionsCount: 3 } }} />);

      // 3. Vérifier l'affichage des résultats finaux
      await waitFor(() => {
        expect(getByText(/Résultats/i) || getByText(/Terminé/i)).toBeTruthy();
        expect(getByText(/2.*3/) || getByText(/Score.*2/)).toBeTruthy();
      });

      // 4. Vérifier que la sauvegarde a été effectuée
      expect(mockMarkRevisionCompleted).toHaveBeenCalledWith(
        mockRevisionQuestions,
        2,
        3
      );

      // 5. Terminer la révision
      const finishButton = getByText('Terminer') || getByText('Fermer');
      if (finishButton) {
        fireEvent.press(finishButton);
        expect(router.back).toHaveBeenCalled();
      }
    });
  });
});